.rbkit-container {
    .rbkit-product-changer {
        display: inline-block;
        margin: 30px;
        padding: 30px;
        border: 1px dashed white;

        .product-changer-image {
            position: relative;
            width: 362px;
            height: 296px;
            margin-bottom: 30px;
            border: 1px dashed white;
            li {
                position: absolute;
                display: none !important;
                width: 100%;
                height: 100%;
                &.active {
                    display: block !important;
                }
            }
        }

        .product-changer-nav {
            display: flex;
            flex-wrap: wrap;
            display: -webkit-flex;
            -webkit-flex-wrap: wrap;
        }
        .product-changer-nav li {
            background-size: contain;
            background-position: center center;
            background-repeat: no-repeat;
            background-color: white;
            width: 100px;
            height: 100px;
            margin-right: 10px;
            margin-top: 10px;
            border: 1px solid #888;
            cursor: pointer;
            transition: all .5s;
        }
        .product-changer-nav li:last-child {
            margin-right: 0;
        }
        .product-changer-nav li.active,
        .product-changer-nav li:hover {
            border-color: #df0000;
            background-color: #df0000;
            transition: all .5s;
        }
        .product-changer-nav li.active:before {
            position:absolute;
            margin-top: -10px;
            margin-left: 40px;
            content: "";
            border-left: 10px solid transparent;
            border-right: 10px solid transparent;
            border-bottom: 10px solid #df0000;
        }
    }
}