.rbkit-container {
    .rbkit-product-slider {
        box-sizing: border-box;
        position: relative;
        height: 290px; //315px
        background-color: transparent;
        >div{
            top:0 !important;
        }

        .product-container {
                height:270px !important;
            a {
                text-decoration: none;
            }
            .header,
            .products-carousel {
                border: none !important;
            }
            .more {
                position: absolute;
                top: auto;
                right: 15px;
                bottom: 15px;
                z-index: 20;
                background-color: white;
                border: 1px solid #dddddd;
                height: 38px;
                border-radius: 4px;
                text-decoration: none;
                color: black;
                &:hover {
                    background-color: #f2f2f2;
                    color: #df0000 !important;
                    span {
                        &:after {
                            background-position: -16px -29px !important;
                        }
                    }
                }
                span {
                    font-size: 16px;
                    height: 36px;
                    line-height: 36px;
                    padding: 0 25px 0 14px;
                    &:after {
                        background: url("//css.redblue.de/MediaDE/skins/campaign-skin-fee/de/images/general/spritescd.png") no-repeat scroll -22px -19px transparent !important;
                        content: "";
                        height: 10px;
                        position: absolute;
                        right: 15px;
                        top: 14px;
                        width: 6px;
                    }
                }
            }
            .products-carousel {
                background-color: transparent;
            }
            .more{
                display: none;
            }
            .header{
                display: block !important;
                h2{
                    padding: 24px 20px 12px 20px !important;
                    color: #000 !important;
                    font-size: 24px !important;
                   margin: 0;
                }
            }
        }

    }

}

