/**
 * @author      Thomas Ott
 *
 * @description this mixin is used for injecting responsive css
 *
 *
 * usage example:
 *
 *  @include respo-width(L) { }
 *  @include respo-width(1280px) { }
 *  @include respo-width(640px,767px) { }
 *
 */

@mixin respo-width($min: 0, $max: 0) {
    $type: type-of($min);

    @if $type == string {
        @if $min == S {
            @media (max-width: 954px) {
                @content;
            }
        } @else if $min == M {
            @media (min-width: 955px) and (max-width: 1199px) {
                @content;
            }
        } @else if $min == L {
            @media (min-width: 1200px) {
                @content;
            }
        }
    } @else if $type == number {
        $query: "all" !default;
        @if $min != 0 and $max != 0 {
            $query: "(min-width: #{$min}) and (max-width: #{$max})";
        } @else if $min != 0 and $max == 0 {
            $query: "(min-width: #{$min})";
        } @else if $min == 0 and $max != 0 {
            $query: "(max-width: #{$max})";
        }
        @media #{$query} {
            @content;
        }
    }
}