
@import "responsive";
@import "tpl-fix-5grid";

.rbkit-container {
    display: block;
    position: relative;
    box-sizing: border-box;
    overflow: hidden;
    border: 1px solid white;
    font-family: $body-text-family, Helvetica, sans-serif;

    @include respo-width(L) {
        width: 1188px;
    }
    @include respo-width(M) {
        width: 948px;
    }
    @include respo-width(S) {
        width: 708px;
    }


    .disabled {
        pointer-events: none;
        cursor: default;
        opacity: .5;
    }
    .hidden {
        display: none;
    }
    .shadow {
        box-shadow: 0 0 20px rgba(0, 0, 0, 0.25);
    }
    .fixed {
        position: fixed;
        top: 50px;
    }

    b,
    strong {
        font-family: $body-text-family-bold;
        font-weight: normal !important;
    }

    p {
        font-family: $body-text-family;
        font-size: $font-size-p;
        line-height: $line-height-p;
        margin-bottom: 12px;
    }
    p:last-child {
        margin-bottom: 0;
    }

    h1, .h1 {
        font-family: $headline-text-family !important;
        font-size: 60px;
        line-height: 1;
        margin-bottom: 12px;
    }
    h2, .h2 {
        font-size: 24px;
        margin-bottom: 12px;
        font-family: mm-text-bold, MMTextProWeb-Bold !important;
    }
    h3, .h3 {
        font-size: 16px;
        margin-bottom: 8px;
        font-family: mm-text-bold, MMTextProWeb-Bold !important;
    }
    h2, .h2, h3, .h3, h4, .h4, h5 ,.h5, h6, .h6 {
        line-height: $line-height;
        font-family: mm-text-bold, MMTextProWeb-Bold !important;
    }
    li {
        font-family: $body-text-family;
        font-size: $font-size-p;
        line-height: $line-height-p;
    }
    .grid3 {
        display: none !important;
        @include respo-width(S) {
            display: block !important;
        }
    }
    .grid4 {
        display: none !important;
        @include respo-width(M) {
            display: block !important;
        }
    }
    .grid5 {
        display: none !important;
        @include respo-width(L) {
            display: block !important;
        }
    }
}

