.rbkit-container {
    .rbkit-nav {
        font-size: 24px;

        ul {
            list-style: none;
            margin: 0;
            padding: 0;
            width: 100%;
            display: flex;
        }
        li {
            text-align: center;
            width: 100%;

        }
        li a {
            width: 100%;
            display: block;
            padding: 5px 0 8px;
            line-height: 25px;
            text-decoration: none;
            background-color: #df0000;
            color: white;
            font-size: 24px;
        }
        li a:hover,
        li a.active {
            background-color: white;
            color: #df0000;
        }
    }
}