.rbkit-container {
    .aspect-16-to-9 {
        @include respo-width(L) {
            height: 667px;
        }
        @include respo-width(M) {
            height: 532px;
        }
        @include respo-width(S) {
            height: 397px;
        }

    }

    .rb-cl-stage-thumbnails {
        .rbkit-slider {
            padding: 10px 60px;
            .next{
                right: -40px !important;
            }
            .prev{
                left: -40px !important;
            }
        }

        .video {
            margin: 20px 20px 0 20px;
        }
        .item {
            margin: 10px;
            cursor: pointer;
            position: relative;
            overflow: hidden;
            .image {
                &:before {
                    content: "";
                    width: 60px;
                    height: 60px;
                    background: rgba(255, 255, 255, 0.7);
                    border-radius: 50%;
                    position: absolute;
                    top: calc(50% - 30px);
                    left: calc(50% - 30px);
                    opacity: 0;
                    pointer-events: none;
                }
                &:after {
                    content: "";
                    border: 15px solid transparent;
                    border-left: 25px solid #0b4b83;
                    position: absolute;
                    top: calc(50% - 15px);
                    left: calc(50% - 10px);
                    opacity: 0;
                    pointer-events: none;
                }
                img {
                    outline: none;
                    border: 5px solid #fff;
                    &.active-video {
                        border-color: #0b4b83;
                    }
                }
                p {
                    text-align: center;
                }

                &:hover {
                    &:before, &:after {
                        opacity: 1;
                    }
                }
            }

        }

    }
}