// Default Variables

$slick-dot-color: white !default;
$slick-dot-color-active: #df0000 !default;
$slick-dot-size: 15px !default;
$slick-dot-border-thickness: 0 !default;
$slick-opacity-default: 1 !default;
$slick-opacity-on-hover: 1 !default;
$slick-opacity-not-active: 1 !default;
$slick-dot-bottom-offset: 15px !default;


/* Dots */

.slick-dots {
    position: absolute;
    bottom: $slick-dot-bottom-offset;
    list-style: none;
    display: block;
    text-align: center;
    padding: 0;
    width: 100%;
    li {
        position: relative;
        display: inline-block;
        height: 20px;
        width: 20px;
        margin: 0 5px;
        padding: 0;
        cursor: pointer;
        button {
            border: 0;
            background: transparent;
            display: block;
            height: 20px;
            width: 20px;
            outline: none;
            line-height: 0px;
            font-size: 0px;
            color: transparent;
            padding: 5px;
            cursor: pointer;
            &:hover, &:focus {
                outline: none;
                &:before {
                    opacity: $slick-opacity-on-hover;
                    background-color: $slick-dot-color-active;
                    border: $slick-dot-border-thickness solid $slick-dot-color;
                }
            }
            &:before {
                position: absolute;
                top: 0;
                left: 0;
                content: "";
                width: $slick-dot-size;
                height: $slick-dot-size;
                background-color: $slick-dot-color;
                border-radius: 20px;
                border: $slick-dot-border-thickness solid $slick-dot-color-active;
            }
        }
        &.slick-active button:before {
            background-color: $slick-dot-color-active;
            opacity: $slick-opacity-default;
            border: $slick-dot-border-thickness solid $slick-dot-color;
        }
    }
}

