@import "../external/slick/slick";
@import "../external/slick/slick-theme-mm";

.rbkit-container {
    .rbkit-slider {
        position: relative;
        padding-bottom: 35px;
        img {
            height: auto;
            width: 100%;
        }
        .prev, .next {
            position: absolute;
            top: 50% !important;
            margin-top: -20px;
        }


                .slick-dots {
                    padding: 0;
                    margin: 0;
                    bottom: -25px;
                    li{
                        height: 15px;
                        width: 15px;
                        margin: 0 5px;
                        button{
                            width:100%;
                            height:100%;
                            box-sizing: border-box;
                            padding: 0;
                            border-radius: 50%;
                            border: 2px solid #df0000;
                            background: #fff;
                            &:before{
                                display: none;
                            }
                        }
                        &.slick-active, &:hover{
                            button{
                                background: #df0000;
                            }
                        }
                    }
                }

    }
}