@import "responsive";

$body-text-family: 'mm-text-regular' !default;
$body-text-family-bold: 'mm-text-bold' !default;
$headline-text-family: 'mm-headline' !default;
$icon-font: 'mm-icons' !default;
$line-height: 1.25 !default;
$font-size-p: 16px !default;
$line-height-p: 1.25 !default;

.rbkit-container {
    .hide-on-desktop{
        display: none !important;
    }
    .button.gray.anchor {
        height: 28px !important;
        span {
            height: 28px !important;
            font-size: 13px !important;
            line-height: 26px !important;
            padding: 0 25px 0 12px !important;
            &::after {
                content: "\e606";
                font-size: 9px;
                top: 10px;
                right: 8px;
                position: absolute;
            }
        }

    }
    .button.primary {
        span {
            &::after {
                font-family: mm-icons;
                content: "\e603";
                color: white;
                font-size: 12px;
                position: absolute;
                right: 13px;
                top: 1px;
            }
        }
    }

    .section-abbinder {
        box-sizing: border-box;
        display: flex;
        justify-content: space-between;
        @include respo-width(S) {
            flex-wrap: wrap;
        }

        div {
            flex-grow: 1;
            flex-basis: 0;
            &:not(:last-child) {
                margin-right: 12px;
            }
            @include respo-width(S) {
                flex-basis: auto;
                &:not(:last-child) {
                    margin-right: 0;
                }
            }
            box-sizing: border-box;
            border: 1px solid #ddd;
            background-color: #fff;
            padding: 30px 20px 30px 40px;
            @include respo-width(M) {
                padding: 20px 15px 20px 30px;
            }
            @include respo-width(S) {
                padding: 15px 10px 15px 20px;
            }
            margin-bottom: 12px;

            h2 {

                font-size: 24px;
                width: 100%;
                color: black;
            }
            h3 {
                font-size: 20px;
                line-height: 1;
                margin-bottom: 16px;
                font-family: mm-headline, MMHeadlineProWeb-regular !important;
                color: #df0000;
            }
            p {
                font-size: 13px;
                color: #444;
            }
            ul {
                li {
                    display: inline-block;
                    border-right: 1px solid #444;
                    padding-right: 10px;
                    margin: 0 10px 10px 0;
                    &:last-child {
                        border: none;
                    }
                    &:first-child {
                        padding-left: 0;
                    }
                    a {
                        color: #444;
                        font-size: 13px;
                        &:hover {
                            color: #df0000;
                        }
                    }

                }
            }
            .newsletter-form{
                padding-top: 2px;
                fieldset {
                    width: 230px;
                    position: relative;
                    label {
                        height: 0;
                        margin: 0;
                        overflow: hidden;
                        position: absolute;
                        text-indent: -9999px;
                    }
                    input {
                        width: 172px;
                        background: none repeat scroll 0 0 #F2F2F2;
                        border: 1px solid #ddd;
                        border-bottom-left-radius: 4px;
                        -webkit-border-bottom-left-radius: 4px;
                        -moz-border-radius-bottomleft: 4px;
                        border-top-left-radius: 4px;
                        -webkit-border-top-left-radius: 4px;
                        -moz-border-radius-topleft: 4px;
                        -webkit-box-shadow: inset 1px 1px 4px 0 rgba(0, 0, 0, 0.1);
                        -moz-box-shadow: inset 1px 1px 4px 0 rgba(0, 0, 0, 0.1);
                        box-shadow: inset 1px 1px 4px 0 rgba(0, 0, 0, 0.1);
                        color: #000;
                        float: left;
                        height: 38px;
                        line-height: normal;
                        margin: -2px 0 0;
                        outline: medium none;
                        padding: 0 10px;
                        width: 195px;
                    }

                    button {
                        -webkit-border-radius: 0 4px 4px 0 !important;
                        -moz-border-radius: 0 4px 4px 0 !important;
                        -ms-border-radius: 0 4px 4px 0 !important;
                        border-radius: 0 4px 4px 0 !important;
                        background: #fff;
                        border: 1px solid #ddd;
                        height: 38px !important;
                        overflow: hidden;
                        text-indent: -9999px;
                        top: -2px;
                        width: 34px;
                        box-sizing: border-box;
                        &:before {
                            color: #000;
                            font-size: 15px;
                            height: 17px;
                            left: 13px;
                            overflow: hidden;
                            padding-top: 1px;
                            position: absolute;
                            text-indent: 0;
                            top: 10px;
                            width: 9px;
                            font-family: 'mm-icons';
                            content: "\e603";
                        }

                    }
                }
            }


        }
    }

    .rb-product-info {
        .rating {
            display: block;
            margin-bottom: 10px;
        }
        .product-image-container {
            position: relative;
            .ee-wrapper {
                position: absolute;
                top: 20px;
                left: 0;
            }
        }
        .brand-img{
            display: block;
            margin-bottom: 10px;
        }
        .price-container {
            display: flex;
            flex-wrap: wrap;
            .price {
                width: auto !important;
            }
            .price-old {
                margin-bottom: 0 !important;
            }
            .price-old-info {
                position: absolute;
                right: -20px;
                top: 0;
                i {
                    font-size: 12px;
                    color: #000;
                    vertical-align: top;
                    line-height: 1;
                    font-family: mm-text-bold, MMTextProWeb-Bold;
                }
            }
            .vat {
                margin: -5px 0 5px;
                font-size: 12px;
                width: 100%;
            }
        }
        .product-image{
            margin-bottom: 10px;
        }
        .product-features {
            margin-bottom: 10px;
            p {
                display: flex;
                margin-bottom: 0;
                &.article-number {
                    margin-bottom: 12px;
                }
                .feature-name {
                    width: 200px;
                    display: inline-block;
                }
                .feature-value {
                    width: 360px;
                    display: inline-block;
                }
            }
        }
        .button {
            margin-right: 10px;
        }
        .button.add-to-cart {
            height: 38px !important;
        }
    }

}


.cms-products-grid .pavailability li{
    font-size: 11px;
    .ct-products-grid>li .cart-checkbox input+label {
        font-size: 13px !important;
    }
}
.ct-modal .ct-modal-content .template-container .product-info>h1 {
    font-family: mm-text-bold, MMTextProWeb-Bold !important;
    text-align: left;
}
.ct-modal .ct-modal-content .template-container .aside .price-box .box.infobox.availability li {
    font-size: 11px;
}
.cms-products-grid .ct-products-grid>li .cart-checkbox input+label {
    font-size: 13px;
}
.cms-products-grid .ct-products-grid>li .base-info h3,
.ct-modal .ct-modal-content .template-container .product-info .model .group dd,
.ct-modal .ct-modal-content .template-container .product-info .product-details dt,
.ct-modal .ct-modal-content .template-container .product-info .product-details dd {
    text-align: left;
}
