#rbkit-layer-container {
    display: none;
}

.featherlight {
    * {
        box-sizing: border-box;
    }
    display: none;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 2147483647;
    text-align: center;
    white-space: nowrap;
    cursor: pointer;
    background: #333;
    background: rgba(0, 0, 0, 0);
    &:last-of-type {
        background: rgba(0, 0, 0, 0.45);
    }
    &:before {
        content: '';
        display: inline-block;
        height: 100%;
        vertical-align: middle;
        margin-right: -0.25em;
    }
    .featherlight-content {
        position: relative;
        text-align: left;
        vertical-align: middle;
        display: inline-block;
        overflow-x: hidden;
        overflow-y: auto;
        padding: 15px 15px 0;
        border-bottom: 15px solid transparent;
        margin-left: 5%;
        margin-right: 5%;
        max-height: 95%;
        background: #fff;
        cursor: auto;
        white-space: normal;

        box-shadow: 1px 1px 10px 0 rgba(0, 0, 0, 0.4);

        h2 {
            box-sizing: border-box;
            font-size: 24px !important;
            font-family: mm-text-bold !important;
            padding: 5px 50px 20px 15px;
            display: block;
            border-bottom: 1px solid #ddd;
            margin: 0 -15px 15px -15px;
        }
    }
    .featherlight-inner {
        display: block;
    }
    .featherlight-close-icon {
        position: absolute;
        z-index: 9999;
        top: 0;
        right: 0;
        cursor: pointer;
        text-align: center;
        font-family: mm-icons;
        line-height: 25px;
        margin: 10px 15px;
        padding: 5px;
        height: 36px;
        width: 36px;
        font-size: 14px;
        border-radius: 30px;
        background: #fff;
        color: #000;
        border: 1px solid #ddd;
        &:hover {
            background-color: #f2f2f2;
            color: #df0000;
        }
    }
    .featherlight-image {
        width: 100%;
    }
    .featherlight-iframe .featherlight-content {
        border-bottom: 0;
        padding: 0;
    }
    iframe {
        border: none;
    }
}
