.rbkit-container {
    .rbkit-accordion {
        &:last-child {
            border-bottom: 1px solid #ddd;
        }
        dt {
            position: relative;
            box-sizing: border-box;
            font-size: 20px;
            cursor: pointer;
            padding: 15px 0 18px 50px;
            background-repeat: no-repeat;
            background-size: 30px;
            background-position: top 10px left 10px;
            background-color: white;
            border-top: 1px solid #ddd;
            font-family: $body-text-family-bold;
            &:before {
                content: "\e603";
                font-family: $icon-font;
                font-size: 18px;
                line-height: 1;
                position: absolute;
                top: 8px;
                left: 9px;
                border: 1px solid #ddd;
                width: 18px;
                height: 18px;
                padding: 6px;
                text-align: center;
                border-radius: 100px;
                background-color: white;
                transition: .25s;
                transform: rotate(90deg);
            }
            &:hover {
                &:before {
                    color: #df0000;
                    background-color: #eee;
                }
            }
            &.active {
                &:before {
                    transform: rotate(-90deg);
                }
            }
        }
        dd {
            background-color: white;
            padding: 0 0 20px 50px;
            h2 {
                font-size: 16px;
                margin-bottom: 8px;
            }
        }
    }
}